"use client";

import { signIn, signOut, useSession } from "next-auth/react";
import Link from "next/link";
import { useState } from "react";
import { Menu, X, LayoutDashboard, Shield } from "lucide-react";

export default function Navbar() {
  const { data: session } = useSession();
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <>
      {/* Navbar */}
      <nav className="bg-white shadow-md fixed top-0 left-0 w-full z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            {/* Logo = Trang chủ */}
            <div className="flex items-center">
              <Link href="/" className="text-2xl font-bold text-blue-600 hover:opacity-80 transition">
                ToolWeb
              </Link>
            </div>

            {/* Desktop Menu */}
            <div className="hidden md:flex space-x-4">
              {session ? (
                <>
                  <Link
                    href="/dashboard"
                    className="flex items-center space-x-2 bg-gray-100 px-4 py-2 rounded-lg text-gray-700 hover:bg-blue-100 hover:text-blue-500 transition"
                  >
                    <LayoutDashboard size={18} />
                    <span>Dashboard</span>
                  </Link>

                  {session.user?.role === "admin" && (
                    <Link
                      href="/admin"
                      className="flex items-center space-x-2 bg-gray-100 px-4 py-2 rounded-lg text-gray-700 hover:bg-red-100 hover:text-red-500 transition"
                    >
                      <Shield size={18} />
                      <span>Admin</span>
                    </Link>
                  )}

                  <button
                    onClick={() => signOut()}
                    className="bg-red-500 text-white px-4 py-2 rounded-lg hover:bg-red-600 transition"
                  >
                    Đăng xuất
                  </button>
                </>
              ) : (
                <button
                  onClick={() => signIn("google")}
                  className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 transition"
                >
                  Đăng nhập
                </button>
              )}
            </div>

            {/* Mobile Menu Button */}
            <div className="md:hidden">
              <button onClick={() => setMenuOpen(!menuOpen)} className="text-gray-700">
                {menuOpen ? <X size={28} /> : <Menu size={28} />}
              </button>
            </div>
          </div>
        </div>

        {/* Mobile Menu */}
        {menuOpen && (
          <div className="md:hidden bg-white shadow-md py-4 px-6 space-y-4 absolute top-16 left-0 w-full">
            {session ? (
              <>
                <Link
                  href="/dashboard"
                  className="block text-gray-700 hover:text-blue-500 flex items-center space-x-2"
                >
                  <LayoutDashboard size={18} />
                  <span>Dashboard</span>
                </Link>

                {session.user?.role === "admin" && (
                  <Link
                    href="/admin"
                    className="block text-gray-700 hover:text-red-500 flex items-center space-x-2"
                  >
                    <Shield size={18} />
                    <span>Admin</span>
                  </Link>
                )}

                <button
                  onClick={() => signOut()}
                  className="block w-full text-left bg-red-500 text-white px-4 py-2 rounded-lg hover:bg-red-600 transition"
                >
                  Đăng xuất
                </button>
              </>
            ) : (
              <button
                onClick={() => signIn("google")}
                className="block w-full text-center bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 transition"
              >
                Đăng nhập
              </button>
            )}
          </div>
        )}
      </nav>

      {/* Padding tránh navbar đè lên nội dung */}
      <div className="pt-16"></div>
    </>
  );
}
